import React from "react";
import styled from "styled-components";
import SearchIcon from "@mui/icons-material/Search";
import Badge from "@mui/material/Badge";
import ShoppingCartOutlinedIcon from "@mui/icons-material/ShoppingCartOutlined";
import LanguageIcon from "@mui/icons-material/Language";
import { mobile } from "../responsive";
import { Link } from "react-router-dom";

const Container = styled.div`
  height: 90px;
  ${mobile({ height: "50px", padding: "5px 10px 0px 0px" })}
`;

const Wrapper = styled.div`
  padding: 15px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  ${mobile({ padding: "10px 0px" })}
`;

const NavContainer = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  ${mobile({ flex: "0" })}
`;

const Language = styled.span`
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  cursor: pointer;
  color: #eb5424;
  ${mobile({ display: "none" })}
`;

const SearchContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.5px solid lightgray;
  display: flex;
  margin-left: 25px;
  padding: 5px;
  ${mobile({ display: "none" })}
`;

const Input = styled.input`
  border: none;
  outline: none;
`;

const LogoContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  ${mobile({
    flex: "2",
  })}
`;

const Logo = styled.p`
  font-size: 36px;
  font-weight: 600;
  color: #555;
  ${mobile({ fontSize: "18px", marginRight: "10px" })}
`;

const MenuContainer = styled.ul`
  display: flex;
  ${mobile({
    flexDirection: "row",
    fontSize: "10px",
  })}
`;
const Menu = styled.li`
  padding: 3px 10px 0px 10px;
  font-size: 14px;
  color: #eb5424;
  ${mobile({ padding: "3px 3px 0px 3px", fontSize: "10px" })}
`;

const ToolContainer = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  ${mobile({
    flexDirection: "column",
    flexWrap: "Wrap",
    maxHeight: "30px",
    marginRight: "10px",
  })}
`;

const MenuItem = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  cursor: pointer;
  margin-left: 25px;
  color: #555;
  ${mobile({
    fontSize: "10px",
    marginLeft: "0px",
    flexDirection: "row",
  })}
`;

const Navbar = () => {
  return (
    <Container>
      <Wrapper>
        <NavContainer>
          <Language>
            <LanguageIcon />
          </Language>
          <SearchContainer>
            <Input placeholder="Search" />
            <SearchIcon style={{ color: "#eb5424", fontSize: "16px" }} />
          </SearchContainer>
        </NavContainer>
        <LogoContainer>
          <Link to="/fashion-e-commerce-react">
            <Logo>Fashion</Logo>
          </Link>
          <MenuContainer>
            <Menu>
              <Link to="/women">WOMEN</Link>
            </Menu>
            <Menu>
              <Link to="/men">MEN</Link>
            </Menu>
            <Menu>
              <Link to="/kids">KIDS</Link>
            </Menu>
            <Menu>
              <Link to="/accessories">ACCESSORIES</Link>
            </Menu>
          </MenuContainer>
        </LogoContainer>
        <ToolContainer>
          <Link to="/register">
            <MenuItem>
              <p>New Member</p>
            </MenuItem>
          </Link>
          <Link to="/login">
            <MenuItem>Login</MenuItem>
          </Link>
          <Link to="/cart">
            <MenuItem>
              <Badge badgeContent={2} color="primary">
                <ShoppingCartOutlinedIcon style={{ color: "#eb5424" }} />
              </Badge>
            </MenuItem>
          </Link>
        </ToolContainer>
      </Wrapper>
    </Container>
  );
};

export default Navbar;
