import React from "react";
import styled from "styled-components";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
import PinterestIcon from "@mui/icons-material/Pinterest";
import LocationIcon from "@mui/icons-material/LocationOn";
import PhoneIcon from "@mui/icons-material/Phone";
import EmailIcon from "@mui/icons-material/EmailOutlined";
import { mobile } from "../responsive";
import { Link } from "react-router-dom";

const Container = styled.div`
  display: flex;
  height: 35vh;
  background-color: #333;
  padding: 15px;
  ${mobile({ flexDirection: "column", height: "100%" })}
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 20px;
`;

const Logo = styled.p`
  font-size: 36px;
  font-weight: 600;
  color: white;
  margin-bottom: 5px;
  ${mobile({ fontSize: "24px" })}
`;

const Desc = styled.p`
  font-size: 14px;
  letter-spacing: 1.5px;
  margin: 20px 0px;
  line-height: 1.8;
  color: #999;
  ${mobile({ lineHeight: "1.5" })}
`;

const SocialContainer = styled.div`
  display: flex;
  ${mobile({ justifyContent: "flextStart" })}
`;

const SocialIcon = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  color: white;
  background-color: #${(props) => props.color};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  border: 1.5px solid white;
  cursor: pointer;
`;

const Center = styled.div`
  flex: 1;
  padding: 20px;
`;

const Title = styled.h3`
  margin-bottom: 30px;
  color: white;
`;

const List = styled.ul`
  display: flex;
  flex-wrap: wrap;
`;

const ListItem = styled.li`
  width: 50%;
  margin-bottom: 15px;
  cursor: pointer;
  color: #999;
`;

const Right = styled.div`
  flex: 1;
  padding: 20px;
`;

const ContactItem = styled.div`
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #999;
`;

const IconContainer = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid #eb5424;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  ${mobile({ border: "0px" })}
`;

const Payment = styled.img`
  width: 90%;
  ${mobile({ width: "100%" })}
`;

const Footer = () => {
  return (
    <Container>
      <Left>
        <Link to="/fashion-e-commerce-react">
          <Logo>FASHION</Logo>
        </Link>
        <Desc>
          There are many variantions of passages of Lorem ipsum dolor sit, amet
          consectetur adipisicing elit. Rerum vel, laudantium dignissimos
          delectus minima, exercitationem voluptas temporibus ipsa repellendus.
        </Desc>
        <SocialContainer>
          <SocialIcon color="3b5999">
            <FacebookIcon />
          </SocialIcon>
          <SocialIcon color="55acee">
            <TwitterIcon />
          </SocialIcon>
          <SocialIcon color="e4405f">
            <InstagramIcon />
          </SocialIcon>
          <SocialIcon color="e60023">
            <PinterestIcon />
          </SocialIcon>
        </SocialContainer>
      </Left>
      <Center>
        <Title>Useful Links</Title>
        <List>
          <ListItem>
            <Link to="/fashion-e-commerce-react">Home</Link>
          </ListItem>
          <ListItem>
            <Link to="/myAccount">My Account</Link>
          </ListItem>
          <ListItem>
            <Link to="/Women">Women</Link>
          </ListItem>
          <ListItem>
            <Link to="/wishlist">Wishlist</Link>
          </ListItem>
          <ListItem>
            <Link to="/men">Men</Link>
          </ListItem>
          <ListItem>
            <Link to="/cart">Cart</Link>
          </ListItem>
          <ListItem>
            <Link to="/Kids">Kids</Link>
          </ListItem>
          <ListItem>
            <Link to="/tracking">Order Tracking</Link>
          </ListItem>
          <ListItem>
            <Link to="/accessories">Accessories</Link>
          </ListItem>
          <ListItem>
            <Link to="/terms">Terms</Link>
          </ListItem>
        </List>
      </Center>
      <Right>
        <Title>Contact</Title>
        <ContactItem>
          <IconContainer>
            <LocationIcon style={{ fontSize: "18px", color: "#fff" }} />
          </IconContainer>
          Egypt, Giza, Inside The Sphinx, Floor Number 117
        </ContactItem>
        <ContactItem>
          <IconContainer>
            <PhoneIcon style={{ fontSize: "18px", color: "#fff" }} />
          </IconContainer>
          +2 123 456 78
        </ContactItem>
        <ContactItem>
          <IconContainer>
            <EmailIcon style={{ fontSize: "18px", color: "#fff" }} />{" "}
          </IconContainer>
          contact@codna.com
        </ContactItem>
        <Payment src="https://i.ibb.co/7tTgFg8/Payment.png" />
      </Right>
    </Container>
  );
};

export default Footer;
