import React from "react";
import styled from "styled-components";
import { mobile } from "../responsive";

const Container = styled.div`
  flex: 1;
  height: 90vh;
  position: relative;
  margin: 10px 30px;
  border: 0px solid transparent;
  border-radius: 40px 40px 0px 0px;
  overflow: hidden;
  ${mobile({
    height: "27vh",
    margin: "10px 0px",
    borderRadius: "0px",
  })}
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const Info = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 62px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border-bottom: 1px solid #555;
  ${mobile({ height: "40px" })}
`;

const Title = styled.h1`
  background-color: white;
  text-align: left;
  flex: 4;
  color: #555;
  font-size: 20px;
  letter-spacing: 8px;
  ${mobile({ fontSize: "16px", flex: "3" })}
`;

const Button = styled.button`
  flex: 1;
  float: right;
  border: none;
  color: #eb5424;
  background-color: white;
  font-weight: bold;
  cursor: pointer;
`;

const CategoryItem = ({ item }) => {
  return (
    <Container>
      <Image src={item.img} />
      <Info>
        <Title>{item.title}</Title>
        <Button>SHOP NOW</Button>
      </Info>
    </Container>
  );
};

export default CategoryItem;
