import React from "react";
import styled from "styled-components";
import RemoveIcon from "@mui/icons-material/Remove";
import AddIcon from "@mui/icons-material/Add";
import AddCartIcon from "@mui/icons-material/AddShoppingCart";
import { mobile } from "../responsive";

const Container = styled.div`
  height: calc(100vh - 120px);
`;

const Wrapper = styled.div`
  padding: 50px;
  display: flex;
  ${mobile({ flexDirection: "column", padding: "0px" })}
`;

const ImgContainer = styled.div`
  flex: 1;
`;

const Image = styled.img`
  width: 100%;
  height: 70vh;
  object-fit: cover;
  ${mobile({ height: "55vh" })}
`;

const InfoContainer = styled.div`
  flex: 1;
  padding: 0px 50px;
  ${mobile({ padding: "0px 20px" })}
`;

const Title = styled.h1`
  font-size: 40px;
  font-weight: 400;
  color: #555;
  letter-spacing: 4px;
  ${mobile({
    fontSize: "25px",
    paddingTop: "15px",
    fontWeight: "600",
    margin: "0px",
  })}
`;

const Desc = styled.p`
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 1.5px;
  line-height: 1.7;
  color: #555;
  margin: 20px 0px;
  ${mobile({
    fontSize: "16px",
    textAlign: "justify",
    letterSpacing: "0.1px",
    margin: "10px 0px",
  })}
`;

const Price = styled.span`
  font-weight: 100;
  font-size: 40px;
  color: #eb5424;
`;

const FilterContainer = styled.div`
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: 40px 0px;
  ${mobile({ flexWrap: "wrap" })}
`;

const Filter = styled.div`
  display: flex;
  align-items: center;
  margin-right: 100px;
  ${mobile({ marginRight: "20px", marginBottom: "20px" })}
`;

const FilterTitle = styled.span`
  font-size: 18px;
  font-weight: 400;
  margin-right: 5px;
  color: #eb5424;
`;

const FilterColor = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #555;
  margin: 5px;
  background-color: ${(props) => props.color};
  cursor: pointer;
`;

const FilterSize = styled.select`
  margin: 5px;
  padding: 2px 2px;
`;

const AddContainer = styled.div`
  width: 61%;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const AmountContainer = styled.div`
  display: flex;
  align-items: center;
  font-weight: 700;
`;

const Amount = styled.option`
  width: 50px;
  height: 20px;
  border: 1px solid #555;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 10px;
  font-size: 18px;
  font-weight: 500;
  color: #eb5424;
`;

const Button = styled.button`
  width: 170px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  padding: 10px 15px;
  border: 1px solid #eb5424;
  background-color: white;
  color: #eb5424;
  cursor: pointer;
  transition: 0.2s;
  &:hover {
    background-color: #eb5424;
    border: 1px solid transparent;
    color: white;
  }
  ${mobile({ margin: "20px auto" })}
`;

const ProductPage = () => {
  return (
    <Container>
      <Wrapper>
        <ImgContainer>
          <Image src="https://i.ibb.co/6FMgDry/Cropped-shot-of-a-group-of-unrecognizable-people-wearing-jeans-while-standing-in-a-row.jpg" />
        </ImgContainer>
        <InfoContainer>
          <Title>Denim Jumpsuit</Title>
          <Desc>
            Lorem ipsum dolor sit amet consectetur, adipisicing elit. Magnam
            aspernatur culpa ut sequi fugit recusandae, porro saepe nesciunt
            impedit, nostrum repellat accusamus officiis. Nobis rem iusto facere
            similique totam magni!
          </Desc>
          <Price>$ 20</Price>
          <FilterContainer>
            <Filter>
              <FilterTitle>Color:</FilterTitle>
              <FilterColor color="White" />
              <FilterColor color="darkBlue" />
              <FilterColor color="Gray" />
              <FilterColor color="Brown" />
              <FilterColor color="Black" />
            </Filter>
            <Filter>
              <FilterTitle>Size:</FilterTitle>
              <FilterSize>
                <option>XS</option>
                <option>S</option>
                <option>M</option>
                <option>L</option>
                <option>XL</option>
              </FilterSize>
            </Filter>
            <AddContainer>
              <AmountContainer>
                <RemoveIcon style={{ color: "#eb5424", cursor: "pointer" }} />
                <Amount>1</Amount>
                <AddIcon style={{ color: "#eb5424", cursor: "pointer" }} />
              </AmountContainer>
            </AddContainer>
          </FilterContainer>
          <Button>
            ADD TO CART
            <AddCartIcon />
          </Button>
        </InfoContainer>
      </Wrapper>
    </Container>
  );
};

export default ProductPage;
