import React from "react";
import styled from "styled-components";
import ShoppingIcon from "@mui/icons-material/ShoppingCartOutlined";
import SearchIcon from "@mui/icons-material/SearchOutlined";
import HeartIcon from "@mui/icons-material/FavoriteBorderOutlined";
import { mobile } from "../responsive";

const Info = styled.div`
  opacity: 0;
  width: 300px;
  height: 300px;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 15px;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease;
  cursor: pointer;
  ${mobile({ width: "120px", height: "120px", left: "20px" })}
`;

const Container = styled.div`
  flex: 1;
  margin: 20px;
  min-width: 300px;
  height: 300px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  &:hover ${Info} {
    opacity: 1;
  }
  ${mobile({ minWidth: "120px", height: "120px", margin: "5px" })}
`;

const Circle = styled.div`
  width: 300px;
  height: 300px;
  margin: 10px;
  border-radius: 10px;
  background: radial-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6)),
    url("https://i.ibb.co/y6WyFv9/61bc2b8797c41352ca99850c4b9f287d.jpg");
  position: absolute;
  ${mobile({ width: "120px", height: "120px" })}
`;

const Image = styled.img`
  height: 100%;
  z-index: 2;
`;

const Icon = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  transition: all 0.3s ease;
  &:hover {
    background-color: #e9f5f5;
    transform: scale(1.2);
  }
  ${mobile({ width: "33px", height: "33px", margin: "3px" })}
`;

const Product = ({ item }) => {
  return (
    <Container>
      <Circle />
      <Image src={item.img} />
      <Info>
        <Icon>
          <ShoppingIcon style={{ color: "#eb5424" }} />
        </Icon>
        <Icon>
          <SearchIcon style={{ color: "#eb5424" }} />
        </Icon>
        <Icon>
          <HeartIcon style={{ color: "#eb5424" }} />
        </Icon>
      </Info>
    </Container>
  );
};

export default Product;
